@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --primary-50: 240, 249, 255;
    --primary-100: 224, 242, 254;
    --primary-200: 186, 230, 253;
    --primary-300: 125, 211, 252;
    --primary-400: 56, 189, 248;
    --primary-500: 14, 165, 233;
    --primary-600: 2, 132, 199;
    --primary-700: 3, 105, 161;
    --primary-800: 7, 89, 133;
    --primary-900: 12, 74, 110;
    --primary-950: 8, 47, 73;
    
    --secondary-50: 248, 250, 252;
    --secondary-100: 241, 245, 249;
    --secondary-200: 226, 232, 240;
    --secondary-300: 203, 213, 225;
    --secondary-400: 148, 163, 184;
    --secondary-500: 100, 116, 139;
    --secondary-600: 71, 85, 105;
    --secondary-700: 51, 65, 85;
    --secondary-800: 30, 41, 59;
    --secondary-900: 15, 23, 42;
    --secondary-950: 2, 6, 23;
  }

  html {
    scroll-behavior: smooth;
  }

  body {
    @apply bg-white dark:bg-secondary-900 text-secondary-900 dark:text-white rtl:text-right;
  }

  /* العناصر التي تحتاج إلى تغيير في وضع RTL */
  .rtl {
    direction: rtl;
  }
}

@layer components {
  .btn {
    @apply inline-flex items-center justify-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm;
  }

  .btn-primary {
    @apply text-white bg-primary-600 hover:bg-primary-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
  }

  .btn-secondary {
    @apply text-primary-700 bg-primary-100 hover:bg-primary-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-primary-500;
  }

  .input-field {
    @apply block w-full px-3 py-2 border border-secondary-300 dark:border-secondary-600 rounded-md shadow-sm focus:ring-primary-500 focus:border-primary-500 dark:bg-secondary-700 dark:text-white;
  }

  .form-label {
    @apply block text-sm font-medium text-secondary-700 dark:text-secondary-300 mb-1;
  }
} 